  padding: 0;
  border: 0;
  font-size: 16px;
  font: inherit;
  font-weight: normal;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
ol,
ul,
li {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
th,
td {
  vertical-align: middle;
}
/* custom */
a {
  font-size: 14px;
  outline: none;
  color: #555555;
  text-decoration: none;
}
a:hover {
  color: #222222;
}
a:focus {
  outline: none;
}
/* //消除button的默认样式 */
button,
input {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  outline: none; /* //消除默认点击蓝色边框效果 */
}
input:focus,
select:focus,
textarea:focus {
  border: none;
  outline: -webkit-focus-ring-color auto 0;
}
/* end */

